@font-face {
    font-family: 'Embroidery';
    src: local('Embroidery'), url(/silvia-peligro/static/media/Embroidery.16dc9400.ttf) format('truetype');
}

@font-face {
    font-family: 'Spot-Matrix';
    src: local('Spot-Matrix'), url(/silvia-peligro/static/media/Spot-Matrix.68a4c297.ttf) format('truetype');
}
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1em}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}body::-webkit-scrollbar{display:none}body{-ms-overflow-style:none}.content-wrap{width:100vw;display:flex;flex-wrap:wrap}

.main-nav{width:100vw;height:12vh;position:fixed;z-index:101;top:0;left:0;background:white}.main-nav__menu_wrapper{width:100%;height:100%;display:flex;align-items:center;justify-content:space-between;padding:0 1em;box-sizing:border-box}.main-nav__logo{height:10vh}.main-nav__logo img{height:100%}.main-nav__hamburger{display:block;width:26px;height:16px;position:relative;z-index:102}.main-nav__hamburger i{-webkit-transition:1s;transition:1s;display:block;position:absolute;width:26px;height:2px;background-color:#161616;border-radius:5px}.main-nav__hamburger i:nth-of-type(1){-webkit-transition:1s;transition:1s;top:16px}.main-nav .nav-collapse{background:#161616;position:fixed;top:0;right:-80vw;width:80vw;height:100vh;-webkit-transition:right 1s;transition:right 1s;z-index:101;display:grid;grid-template-columns:repeat(9, 1fr);grid-template-rows:repeat(12, 1fr)}.main-nav .nav-collapse__list{list-style:none;padding:0;margin:0;color:white;grid-column:3/8;grid-row:3/10}.main-nav .nav-collapse__item{margin-bottom:2.3em}.main-nav .nav-collapse__icons{grid-column:3/6;grid-row:11/12;align-self:center}.main-nav .nav-collapse__icons img{width:2.3em;margin-right:0.8em}.showMenu .main-nav__hamburger i{-webkit-transform:rotate(-45deg);transform:rotate(-45deg);top:8px;background-color:white}.showMenu .main-nav__hamburger i:nth-of-type(1){-webkit-transform:rotate(45deg);transform:rotate(45deg);top:8px}.showMenu .nav-collapse{right:0}@media only screen and (min-width: 48em){.main-nav .nav-collapse{width:75vw}}@media only screen and (min-width: 62em){.main-nav__hamburger{display:none}.main-nav .nav-collapse{max-width:85vw;height:12vh;right:2em;background:white;grid-template-columns:repeat(6, 1fr);grid-template-rows:1fr}.main-nav .nav-collapse__list{color:black;grid-column:1/13;grid-row:1/13;display:flex;justify-content:space-between;align-items:flex-end}.main-nav .nav-collapse__item{margin-bottom:1em}.main-nav .nav-collapse__icons{display:none}}.jumbotron{height:50vh}

.mainSlideBanner{width:100%;height:100%}.mainSlideBanner__boxImg{height:42vh;background-size:cover;background-repeat:no-repeat;background-position:center}.mainSlideBanner__txtBox{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin-bottom:9vh;padding:.8rem}.mainSlideBanner__txtBox .title{margin:0;font-size:1.5rem;line-height:1.5rem}.mainSlideBanner__txtBox .paragraph{font-size:0.8rem;line-height:1.1rem;margin:.2rem 0 .5rem 0}.mainSlideBanner__txtBox .see-more-btn{padding:.3rem 2.5rem;font-size:0.8rem;border:none;border-radius:3px;background:#AC90F1;color:white}@media only screen and (min-width: 48em){.mainSlideBanner__boxImg{height:63vh}.mainSlideBanner__txtBox{position:absolute;bottom:9vh;margin-bottom:0;max-height:29vh;min-width:48vw;max-width:55vw;box-shadow:0 0 0 200px rgba(255,255,255,0.5) inset}.mainSlideBanner__txtBox .line-clamp{display:-webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical;overflow:hidden;padding-bottom:0.2rem}}

.footer{grid-row:15 / auto}.footer__border{width:100vw}.footer__border span{display:inline-block;width:100%;overflow:hidden;white-space:nowrap;font-size:2rem;line-height:0.5;color:#AC90F1}.footer .top-border{font-family:Spot-Matrix}.footer .top-border span{font-size:4rem}.footer .middle-border{font-family:Embroidery}.footer__input-wrapper{height:20vh;width:87%;margin:0 auto}.footer__input-wrapper p{text-align:center;font-size:0.8rem}.footer__input-wrapper .custom-input{width:100%;border-radius:5%;margin:0 auto}.footer__input-wrapper .form-control{border-radius:35px 0 0 35px;border-color:#AC90F1;border-right:none;font-size:0.8rem}.footer__input-wrapper .btn{color:black;line-height:1;background:transparent;border-color:#AC90F1;border-left:none;border-radius:0 35px 35px 0 !important;font-size:0.8rem;font-weight:600}.footer__menu{display:flex;flex-wrap:wrap;justify-content:space-between;width:95%;margin:0.5em auto}.footer__menu ul{list-style:none}.footer__menu ul li{margin-bottom:0.3em;color:#43026A;cursor:pointer}.footer__menu img{width:2.3em;margin-right:0.8em;cursor:pointer}.footer__copyrights{background:#43026A;height:8vh;display:grid;grid-template-columns:repeat(4, 1fr);align-items:center;justify-items:center}.footer__copyrights .languages{display:flex;justify-content:flex-start;width:75%}.footer__copyrights .languages button{border:none;border-radius:6px;margin-right:0.5em}.footer__copyrights .date{color:white;font-size:0.7em;grid-column:2/4}@media only screen and (min-width: 37.5em){.footer__input-wrapper .custom-input{width:85%}}@media only screen and (min-width: 48em){.footer__input-wrapper .custom-input{width:55%}.footer__menu{justify-content:center;margin:1.2em auto}.footer__menu ul{width:100%;display:flex;justify-content:center;padding:0 5%}.footer__menu ul li{display:flex;align-items:center}.footer__menu ul li::after{content:'\25C6';color:#43026A;margin:0 1em}.footer__menu ul li:last-child:after{content:'';margin:0}.footer__menu img{margin:0 0.3em}.footer__copyrights .languages{justify-content:flex-start;width:80%}}@media only screen and (min-width: 62em){.footer__input-wrapper .custom-input{width:41%}}

.follow-me{width:100%;font-size:1.1rem;margin-bottom:0;text-align:center}.instagramFeed{margin:.5rem 0 1rem 0;width:100vw;display:flex;flex-wrap:wrap;justify-content:center}.instagramFeed .post{width:50%;max-width:160px;background-size:cover;background-position:center center;background-repeat:no-repeat}.instagramFeed .post:after{content:"";display:block;padding-bottom:100%}@media (min-width: 30em){.instagramFeed .post{width:33%}}

